body {
    font-family: 'Nunito', serif;
}

.Webpage {
    text-align: center;
}

.Bg {
    display: flex;
    position: absolute;
    top: 0;
    /* left: 0; */
    width: 100%;
    height: 100vh;
    z-index: -1;
    justify-content: center;
}

.BgVideo {
    /* width: 100vw; */
    height: 100vh;
    opacity: 0.5;
    text-align: center;

}

.Titlebar {
    margin-top: 5vh;
    margin-bottom: 5vh;
    width: 100vw;
    z-index: 10;
    position: fixed;
    top: 0;
}

.Navbar {
    /* display: flex; */
    /* justify-content: center; */
    gap: 40px;
    padding: 20px;
}

.Navlink {
    text-decoration: none;
    font-family: 'Nunito', serif;
    font-weight: 500;
    font-size: 20px;
    color: #1f1f1f;
    padding: 20px 20px
}

.Navlink:hover {
    font-weight: 800;
    cursor: pointer;
}

.Logo {
    width: 300px;
}

.Content {
    /* padding: 60px; */
    padding-top: 18vh;
    padding-left: 20%;
    padding-right: 20%;
    height: 80vh;
}

.AppLinks {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 160px;
    padding: 20px;
}

.AppLinkCol {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px;
    text-align: center;
}

.AppLinkImg {
    height: 200px;
    width: auto;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.AppLinkBtn {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #E4F7FF;
    color: #1f1f1f;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 16px;
    font-family: 'Nunito', serif;
    font-weight: 700;
}

.InstrImg {
    height: 25vh;
    padding-bottom: 5vh;
}

.GalleryGrid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 20px;
    padding: 20px;
    max-width: 80vw;
}

.GalleryItem {
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
}

.GalleryItem img {
    width: 100%;
    height: auto;
    max-width: 240px;
    border-radius: 8px;
    object-fit: cover;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);

}

.PaperList {
    font-size: 18px;
}
 
h1 {
    font-family: "Nunito", serif;
    font-optical-sizing: auto;
    font-size: 56px;
    font-weight: 700;
    font-style: normal;
}

h2 {
    font-family: "Nunito", serif;
    font-optical-sizing: auto;
    font-size: 42px;
    font-weight: 700;
    font-style: normal;
}

p {
    font-family: "Nunito", serif;
    font-optical-sizing: auto;
    font-size: 20px;
    font-weight: 400;
    font-style: normal;
}

a {
    color: #000000;
}
